}
html{font-size:62.5%}body{font-family:'Noto Sans', sans-serif;font-size:1.6rem;line-height:1.5;word-break:break-word}.inner{padding:0 7vw}li{list-style:none}.siteTitle{line-height:1;font-weight:600;font-size:2.5rem;margin:15px 0}.siteTitle span{font-size:1.5rem;display:inline-block;margin-left:6px}nav ul{display:flex;justify-content:center;color:#fff;background:#222;box-shadow:0 0 5px #666}nav ul li{width:calc(100% / 3);text-align:center}nav ul li:not(:last-child){border-right:1px solid #fff}nav ul li a{display:inline-block;font-weight:600;width:100%;padding:12px 0;transition:0.3s}nav ul li a:hover{background:#666}main{padding:30px 0}.pageTitle{color:#fff;background:#7e2c2f;text-align:center;font-weight:bold;font-size:2.2rem;letter-spacing:0.1em;padding:8px 5px;margin-right:-7vw;margin-left:-7vw;margin-bottom:30px}footer{text-align:center;color:#fff;font-size:1.4rem;background:#222;padding:15px 0}footer a:hover{text-decoration:underline}@media only screen and (max-width: 768px){.inner{padding:0 3vw}nav ul li a{font-size:1.3rem}.pageTitle{margin-right:-3vw;margin-left:-3vw}}@media only screen and (max-width: 480px){.siteTitle span{display:block;margin-top:4px;margin-left:0}}

.itemWrap{display:flex;flex-direction:column;align-items:center;margin:auto}.itemWrap .item{width:500px;max-width:90%;text-align:center;box-shadow:0 0 10px #555}.itemWrap .item:not(:last-child){margin-bottom:30px}.itemWrap .item .itemTitle,.itemWrap .item .itemDetail{font-weight:600}.itemWrap .item .itemTitle{display:flex;justify-content:center;align-items:center;font-size:2rem;color:#fff;background:#222;padding:20px 0}.itemWrap .item .itemTitle.modalTitle{background:#7e2c2f}.itemWrap .item .itemDetail{cursor:pointer;font-size:1.5rem;letter-spacing:0.1em;padding:10px 0;transition:0.3s}.itemWrap .item .itemDetail:hover{background:#ccc}.itemWrap .itemModalLayer{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(20,20,20,0.8)}.itemWrap .itemModal{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:500px;max-width:80vw;background:#fff;box-shadow:0 0 10px #111}.itemWrap .itemModal .itemText{text-align:left;padding:30px 10px}.itemWrap .itemModal .itemBtn{display:block;width:100%;color:#fff;font-weight:600;padding:10px 0}.itemWrap .itemModal .itemBtn:hover{text-decoration:underline}.itemWrap .itemModal .itemDetailBtn{background:#222}.itemWrap .itemModal .itemCloseBtn{background:#999}

.introductionItem{display:flex;box-shadow:0 0 5px #555}.introductionItem:nth-child(2n){flex-direction:row-reverse}.introductionItem:not(:last-child){margin-bottom:20px}.introductionItem .titleContent,.introductionItem .textContent{width:50%;padding:20px 10px}.introductionItem .titleContent{color:#fff;background:#222;font-size:2.2rem;font-weight:600;display:flex;justify-content:center;align-items:center;margin:6px}.introductionItem .textContent{font-size:1.5rem;white-space:pre-wrap}@media only screen and (max-width: 768px){.introductionItem{flex-direction:column;align-items:center}.introductionItem:nth-child(2n){flex-direction:column}.introductionItem .titleContent,.introductionItem .textContent{width:100%}}

.linkWrap{width:800px;max-width:100%;display:flex;flex-wrap:wrap;justify-content:space-around;margin:auto}.linkWrap a{display:block;transition:0.3s}.linkWrap a:hover{opacity:0.7}.linkWrap .linkItem{width:300px;box-shadow:0 0 5px #555}.linkWrap .linkItem:first-child,.linkWrap .linkItem:nth-child(2){margin-bottom:30px}.linkWrap .linkItem .linkImg{width:100%;height:200px}.linkWrap .linkItem .linkTitle{text-align:center;color:#fff;font-weight:600;background:#222;padding:8px 0}.linkWrap .linkItem .linkText{font-size:1.4rem;padding:10px}@media only screen and (max-width: 768px){.linkWrap{flex-direction:column;align-items:center}.linkWrap .linkItem:not(:last-child){margin-bottom:25px}}

